








import { Component, Vue } from 'vue-property-decorator';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { SMAAPass } from 'three/examples/jsm/postprocessing/SMAAPass.js';
import { Mesh, Material } from 'three';

@Component<Scene>({
    mounted() {
        const el = this.$refs.scene as Element;
        this.camera = new THREE.PerspectiveCamera(60, el.clientWidth / el.clientHeight, 1, 1000);
        this.camera.position.set(55, 55, 55);
        this.renderer.setSize(el.clientWidth, el.clientHeight);
        el.appendChild(this.renderer.domElement);

        if ('ontouchstart' in document.documentElement) {
            this.touchScreen = true;
            window.addEventListener('touchend', this.onCursorMove, false);
        } else {
            // Mouse event
            window.addEventListener('mousemove', this.onCursorMove, false);
        }

        // Widnow event
        window.addEventListener('resize', this.onWindowResize);

        // Dodanie loadera plików
        const loader = new GLTFLoader();

        // Draco
        const dracoLoader = new DRACOLoader();
        dracoLoader.setDecoderPath('/gltf/');
        loader.setDRACOLoader(dracoLoader);

        loader.load(
            '/develop_house/untitledDraco.gltf',
            gltf => {
                const model = gltf.scene;
                this.scene.add(model);
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );

        loader.load(
            '/develop_house/sciana.gltf',
            gltf => {
                const model = gltf.scene;
                model.name = 'sciana';
                this.scene.add(model);
                for (const mesh in model.children) {
                    (model.children[mesh] as Mesh).material = new THREE.MeshStandardMaterial({ color: 0xff0000, opacity: 0, transparent: true });
                    this.selectable.push(model.children[mesh] as Mesh);
                }
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );

        // Dodanie światła
        const ambientLight = new THREE.HemisphereLight(0xffffff, 0x222222, 1);
        const mainLight = new THREE.DirectionalLight(0xffffff, 0.5);
        mainLight.position.set(10, 10, 10);
        this.scene.add(ambientLight, mainLight);

        // Tło
        const textureLoader = new THREE.TextureLoader();
        const groundTexture = textureLoader.load('textures/grass.jpg');
        groundTexture.wrapS = groundTexture.wrapT = THREE.RepeatWrapping;
        groundTexture.repeat.set(25, 25);
        groundTexture.anisotropy = 16;
        groundTexture.encoding = THREE.sRGBEncoding;

        const groundMaterial = new THREE.MeshLambertMaterial({ map: groundTexture });

        const mesh = new THREE.Mesh(new THREE.PlaneBufferGeometry(500, 500), groundMaterial);
        mesh.position.y = -1;
        mesh.rotation.x = -Math.PI / 2;
        mesh.receiveShadow = true;

        this.scene.background = new THREE.Color(0xcce0ff);
        this.scene.fog = new THREE.Fog(0xcce0ff, 70, 200);
        this.scene.add(mesh);

        // Sterowanie kamerą za pomoca myszy
        const controls = new OrbitControls(this.camera, this.renderer.domElement);
        controls.enableDamping = true;
        controls.dampingFactor = 0.05;
        controls.screenSpacePanning = false;
        controls.minDistance = 10;
        controls.maxDistance = 5000;

        // Postprocessing
        const composer = new EffectComposer(this.renderer);
        const renderPass = new RenderPass(this.scene, this.camera);
        composer.addPass(renderPass);
        const pass = new SMAAPass(window.innerWidth * this.renderer.getPixelRatio(), window.innerHeight * this.renderer.getPixelRatio());
        composer.addPass(pass);

        // Funkcja renderująca
        const render = () => {
            this.renderer.render(this.scene, this.camera);
        };

        // Główna pętla sceny
        const animate = () => {
            requestAnimationFrame(animate);

            controls.update();
            //composer.render(); // Render postprocessingu
            render(); // Render standardowy
        };

        animate();
    },
    destroyed: function() {
        window.addEventListener('mousemove', this.onCursorMove, false);
        this.renderer.forceContextLoss();
    },
})
export default class Scene extends Vue {
    private camera!: THREE.PerspectiveCamera;
    private renderer: THREE.WebGLRenderer = new THREE.WebGLRenderer({ antialias: true });
    private scene: THREE.Scene = new THREE.Scene();
    private mouse = new THREE.Vector2();
    private raycaster = new THREE.Raycaster();
    selectable: Mesh[] = [];
    currentSelected: number | null = null;
    touchScreen = false;
    n = 0;

    resetOpacity() {
        if (this.currentSelected) {
            const index = this.selectable.findIndex(mesh => mesh.id === this.currentSelected);
            (this.selectable[index].material as Material).opacity = 0;
            this.currentSelected = null;
        }
    }

    onCursorMove(event: any) {
        if (this.touchScreen) {
            this.mouse.x = (window.innerWidth / 2 / window.innerWidth) * 2 - 1;
            this.mouse.y = -(window.innerHeight / 2 / window.innerHeight) * 2 + 1;
            (this.$refs.target as HTMLElement).style.right = window.innerWidth / 2 + 'px';
            (this.$refs.target as HTMLElement).style.top = window.innerHeight / 2 + 'px';
        } else {
            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        }

        // Raycasting
        this.raycaster.setFromCamera(this.mouse, this.camera);

        // Przecięcia wykryte przez raycasting
        const intersects = this.raycaster.intersectObjects(this.selectable, true);
        if (intersects && intersects[0]) {
            this.resetOpacity();
            ((intersects[0].object as Mesh).material as Material).opacity = 0.5;
            ((intersects[0].object as Mesh).material as Material).transparent = true;
            this.currentSelected = intersects[0].object.id;
        } else {
            this.resetOpacity();
            for (const mesh in this.selectable) {
                (this.selectable[mesh].material as Material).opacity = 0;
            }
        }
    }

    onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
    }

    clickCurrentSelected() {
        this.n += 1;
        if (this.currentSelected) {
            this.$router.push('FlatScene');
        }
    }
}
